function calculateExperience(): number {
    const startDate = new Date("2019-08-19");
    const difference = new Date().getTime() - new Date(startDate).getTime();
    const years = new Date(difference).getFullYear() - 1970;
    const months = new Date(difference).getMonth();
    return years + Number(`0.${months}`);
}

export const aboutMe: string = `A result-oriented individual with <b>${calculateExperience()}</b> years of experience in Software Development, seeking a suitable position at a reputed organisation where I can use my extensive knowledge in software design and development and further learn new things and grow as a Software Engineer.`;

